.extra{
    align-self: flex-start;
    padding-bottom: 1rem;
    color: blue;
}
.page{
    color: blue;
    display: flex;
    align-items: center;
}
.search-item-container{
    box-sizing: border-box;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.search-item{
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border: 2px solid rgb(174, 174, 174);
    margin-bottom: 0.5rem;
    box-sizing: border-box;
    border-radius: 10px;
    padding-left: 1rem;
    cursor: pointer;
    overflow: hidden;
}
.search-item:hover{
    background-color: aliceblue;
}
.search-user-img-box{
    width: 3.5rem;
    height: 3.5rem;
    margin-right: 2rem;
}
.search-user-img{
    width: 3.5rem;
    height: 3.5rem;
}
.pagin{
    display: flex;
    justify-content: space-between;
    width: 20rem;
}

@media screen and (max-width: 940px) {
    .search-item{
        height: 5rem;
        padding-left: 0.5rem;
    }
}